import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';

// =============================================================================
// i18n 
// =============================================================================

/**
 * i18n 
 * 
 * 
 * - 
 * - 
 * - 
 * - localStorage 
 * - 
 */

// 
export const SUPPORTED_LANGUAGES = {
  zh: {
    code: 'zh',
    name: '',
    nativeName: '',
    flag: ''
  },
  en: {
    code: 'en', 
    name: 'English',
    nativeName: 'English',
    flag: ''
  }
};

// 
export const DEFAULT_LANGUAGE = 'zh';

// localStorage 
export const STORAGE_KEY = 'lyra_exporter_language';

/**
 * 
 * @param {Object} obj - 
 * @param {string} path -  'welcomePage.title'
 * @param {*} defaultValue - 
 * @returns {*} 
 */
export const getNestedValue = (obj, path, defaultValue = null) => {
  const keys = path.split('.');
  let current = obj;
  
  for (const key of keys) {
    if (current && typeof current === 'object' && key in current) {
      current = current[key];
    } else {
      return defaultValue;
    }
  }
  
  return current;
};

/**
 * 
 * @param {string} text - 
 * @param {Object} params - 
 * @returns {string} 
 */
export const interpolate = (text, params = {}) => {
  if (!text || typeof text !== 'string') {
    return text;
  }
  
  return text.replace(/\{\{(\w+)\}\}/g, (match, key) => {
    return key in params ? params[key] : match;
  });
};

/**
 * 
 * @param {string} languageCode - 
 * @returns {Promise<Object>} 
 */
export const loadLanguagePack = async (languageCode) => {
  try {
    // 
    const module = await import(`./langs/${languageCode}.json`);
    return module.default || module;
  } catch (error) {
    console.warn(`Failed to load language pack for ${languageCode}:`, error);
    
    // 
    if (languageCode === 'en') {
      return {};
    }
    
    // 
    try {
      const fallbackModule = await import(`./langs/${DEFAULT_LANGUAGE}.json`);
      return fallbackModule.default || fallbackModule;
    } catch (fallbackError) {
      console.error('Failed to load fallback language pack:', fallbackError);
      return {};
    }
  }
};

/**
 * 
 * @returns {string} 
 */
export const getSavedLanguage = () => {
  try {
    const saved = localStorage.getItem(STORAGE_KEY);
    if (saved && SUPPORTED_LANGUAGES[saved]) {
      return saved;
    }
  } catch (error) {
    console.warn('Failed to read language from localStorage:', error);
  }
  
  return DEFAULT_LANGUAGE;
};

/**
 * 
 * @param {string} languageCode - 
 */
export const saveLanguage = (languageCode) => {
  try {
    localStorage.setItem(STORAGE_KEY, languageCode);
  } catch (error) {
    console.warn('Failed to save language to localStorage:', error);
  }
};

// =============================================================================
// React 
// =============================================================================

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);